:root {
  --chip-background: #F4F4F4;
  --chip-color: #191919;
  --chip-background-hover: #dadada;
  --chip-color-hover: #191919;
  --chip-action-background: #c1c1c1;
  --chip-action-color: #191919;
}
.dark-side {
  --chip-background: #2b2d30;
  --chip-color: #f3fcff;
  --chip-background-hover: #333439;
  --chip-color-hover: #f3fcff;
  --chip-action-background: #43444a;
  --chip-action-color: #f3fcff;
}
.chip {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  height: 32px;
  font-size: 14px;
  padding: 0 8px;
  line-height: 32px;
  background: var(--chip-background);
  color: var(--chip-color);
  cursor: default;
  border-radius: 4px;
  position: relative;
}
.chip .label {
  white-space: nowrap;
  order: 2;
  margin: 0 1rem 0 0.5rem;
}
.chip .icon {
  order: 1;
  width: 32px;
  height: 32px;
  line-height: 32px;
  vertical-align: middle;
  font-size: 24px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.chip .icon img {
  width: 24px;
  max-height: 24px;
  vertical-align: initial;
}
.chip .action {
  order: 3;
  display: block;
  cursor: pointer;
  width: 24px;
  height: 24px;
  margin-left: auto;
  background: var(--chip-action-background);
  color: var(--chip-action-color);
  border-radius: 4px;
}
.chip .action:before {
  content: "\2716";
  line-height: 24px;
  text-align: center;
  display: block;
}
.chip.pill-chip {
  border-radius: 1rem;
}
.chip.pill-chip .action {
  border-radius: 50%;
}
.chip:hover {
  background-color: rgba(29, 29, 29, 0.1);
}
.chip.readonly .action,
.chip.disabled .action {
  display: none;
}
.chip + .chip {
  margin-left: 8px;
}
