:root {
  --hero-background: #ffffff;
  --hero-color: #191919;
}
.dark-side {
  --hero-background: #1e1f22;
  --hero-color: #dbdfe7;
}
.hero {
  padding: 40px 20px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  background-color: var(--hero-background);
  color: var(--hero-color);
}
.hero .hero-image {
  height: 220px;
  object-fit: cover;
  object-position: center;
}
.hero1 {
  clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
}
.hero2 {
  clip-path: polygon(0 0, 100% 0, 100% 80%, 50% 100%, 0 80%);
}
.hero3 {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
}
.hero4 {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 50% 80%, 0 100%);
}
.hero5 {
  --mask: radial-gradient(67.08px at 50% calc(100% - 90px), #000 99%, #0000 101%) calc(50% - 60px) 0/120px 100%,
            radial-gradient(67.08px at 50% calc(100% + 60px),#0000 99%,#000 101%) 50% calc(100% - 30px)/120px 100% repeat-x;
  -webkit-mask: var(--mask);
  mask: var(--mask);
}
.hero6 {
  --mask: radial-gradient(223.61px at 50% calc(100% - 300px), #000 99%, #0000 101%) calc(50% - 200px) 0/400px 100%,
            radial-gradient(223.61px at 50% calc(100% + 200px),#0000 99%,#000 101%) 50% calc(100% - 100px)/400px 100% repeat-x;
  -webkit-mask: var(--mask);
  mask: var(--mask);
}
@media all and (min-width: 768px) {
  .hero {
    padding: 160px 80px;
  }
}
