:root {
  --tag-background: #f8f8f8;
  --tag-color: #191919;
  --tag-action-background: #dcdcdc;
  --tag-action-color: #191919;
}
.dark-side {
  --tag-background: #53575c;
  --tag-color: #f3fcff;
  --tag-action-background: #2b2d30;
  --tag-action-color: #ffffff;
}
.tag {
  display: inline-flex;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
  margin: 2px;
  padding: 0;
  height: 26px;
  background: var(--tag-background);
  color: var(--tag-color);
  cursor: default;
  border-radius: 4px;
  flex-wrap: nowrap;
}
.tag .title {
  display: flex;
  align-items: center;
  position: relative;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 0.5rem;
  white-space: nowrap;
  height: 100%;
}
.tag .action {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
  aspect-ratio: 1;
  text-align: center;
  cursor: pointer;
  background: var(--tag-action-background);
  color: var(--tag-action-color);
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  transition: all 0.15s ease-in-out;
  line-height: 1;
}
@media (hover: hover) {
  .tag .action:hover {
    font-size: 1.4em;
  }
}
@media (hover: none) {
  .tag .action:active {
    font-size: 1.4em;
  }
}
.tag.short-tag .title {
  max-width: 120px;
}
