:root {
  --sidenav-m3-border-radius: 4px;
  --sidenav-m3-width: 220px;
}
:root {
  --sidenav-m3-background: #f6f6f6;
  --sidenav-m3-color: #191919;
  --sidenav-m3-icon-color: #191919;
  --sidenav-m3-counter-color: #191919;
  --sidenav-m3-background-hover: #cecece;
  --sidenav-m3-color-hover: #0a0a0a;
  --sidenav-m3-background-active: #68a3ff;
  --sidenav-m3-color-active: #ffffff;
  --sidenav-m3-border-color: var(--border-color);
}
.dark-side {
  --sidenav-m3-background: #2b2d30;
  --sidenav-m3-color: #dfe1e5;
  --sidenav-m3-icon-color: #dfe1e5;
  --sidenav-m3-counter-color: #dfe1e5;
  --sidenav-m3-background-hover: #43454a;
  --sidenav-m3-color-hover: #ffffff;
  --sidenav-m3-background-active: #468cff;
  --sidenav-m3-color-active: #ffffff;
  --sidenav-m3-border-color: var(--border-color);
}
.sidenav-m3 {
  display: block;
  position: relative;
  list-style: none inside;
  height: 100%;
  width: var(--sidenav-m3-width);
  margin: 0;
  padding: 0;
  background-color: var(--sidenav-m3-background);
  color: var(--sidenav-m3-color);
}
.sidenav-m3 {
  padding: 10px 0;
}
.sidenav-m3 li {
  margin: 0 6px;
  font-size: 14px;
}
.sidenav-m3 li,
.sidenav-m3 a {
  position: relative;
  color: inherit;
  border-radius: var(--sidenav-m3-border-radius);
}
.sidenav-m3 > li {
  background: transparent;
}
.sidenav-m3 > li.stick-left::before,
.sidenav-m3 > li.stick-right::before {
  content: "";
  display: block;
  position: absolute;
  width: 6px;
  height: 42px;
  border-radius: var(--sidenav-m3-border-radius) 0 0 var(--sidenav-m3-border-radius);
  background-color: inherit;
  top: 0;
  left: -12px;
}
.sidenav-m3 > li.stick-right::before {
  left: auto;
  right: -12px;
  border-radius: 0 var(--sidenav-m3-border-radius) var(--sidenav-m3-border-radius) 0;
}
.sidenav-m3 a {
  display: flex;
  align-items: center;
  padding: 0.625rem 1rem 0.625rem 3rem;
  text-decoration: none;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.sidenav-m3 a:hover {
  text-decoration: none;
}
.sidenav-m3 li + li {
  border-top: 1px solid var(--sidenav-m3-border-color);
}
.sidenav-m3 .icon {
  width: 20px;
  height: 20px;
  font-size: 18px;
  line-height: 20px;
  vertical-align: middle;
  text-align: center;
  position: absolute;
  left: 10px;
  top: 10px;
  bottom: 12px;
  object-fit: cover;
}
.sidenav-m3 .title {
  padding: 20px 10px;
  font-size: 16px;
  font-weight: 500;
  border: 0;
  cursor: default;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.sidenav-m3 .title .action {
  margin-left: auto;
}
@media (hover: hover) {
  .sidenav-m3 li:not(.title):hover {
    background-color: var(--sidenav-m3-background-hover);
    color: var(--sidenav-m3-color-hover);
  }
}
@media (hover: none) {
  .sidenav-m3 li:not(.title):active {
    background-color: var(--sidenav-m3-background-hover);
    color: var(--sidenav-m3-color-hover);
  }
}
.sidenav-m3 li:not(.title).active {
  background-color: var(--sidenav-m3-background-active);
  color: var(--sidenav-m3-color-active);
}
.sidenav-m3 .dropdown-caret {
  margin-left: auto;
}
.sidenav-m3 .d-menu {
  position: relative;
  box-shadow: none;
  width: 100%;
  float: none;
  border-radius: 0!important;
  padding: 0;
  margin: 0;
  border: none;
  border-bottom: 1px solid var(--sidenav-m3-border-color);
}
.sidenav-m3 .d-menu li {
  margin: 0;
}
.sidenav-m3 .d-menu li:not(:first-child) {
  border-top: 1px solid var(--border-color);
}
.sidenav-m3 .d-menu a {
  border-radius: 0!important;
}
