:root {
  --remark-border-radius: 6px;
  --remark-border-color: var(--border-color);
  --remark-background: #ffffff;
  --remark-color: #191919;
}
.dark-side {
  --remark-border-color: var(--border-color);
  --remark-background: #26282e;
  --remark-color: #dbdfe7;
}
.remark {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  position: relative;
  border: 1px solid;
  border-left: 4px solid;
  border-color: var(--remark-border-color);
  border-radius: var(--remark-border-radius);
  color: var(--remark-color);
  background: var(--remark-background);
  padding: 1rem 2rem 1rem 1rem;
  font-size: 14px;
  margin: 1rem 0;
  cursor: default;
  overflow: hidden;
}
.remark .icon {
  margin-right: 1rem;
  font-size: 32px;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 10px;
  left: 10px;
  object-fit: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}
.remark::before {
  font-family: serif;
  content: "”";
  position: absolute;
  top: -10px;
  right: 10px;
  font-size: 48px;
  font-feature-settings: 'onum';
  font-variant-numeric: oldstyle-nums;
}
.remark:has(.icon) {
  min-height: 40px;
  padding-left: 50px;
  padding-right: 50px;
}
